.ring {
  display: flex;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  position: relative;
  box-shadow: -3px -3px 7px #ffffffb2, 3px 3px 5px rgba(94, 104, 121, 0.945);
  overflow: hidden;
}

.ring:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 80%;
  width: 80%;
  border-radius: 50%;
  box-shadow: inset -3px -3px 7px #ffffffb0,
    inset 3px 3px 5px rgba(94, 104, 121, 0.692);
}

.ring-two {
  display: flex;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  position: relative;
  box-shadow: -3px -3px 7px #ffffffb2, 3px 3px 5px rgba(94, 104, 121, 0.945);
  overflow: hidden;

}

.ring-two:after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  height: 80%;
  width: 80%;
  border-radius: 50%;
  box-shadow: inset -3px -3px 7px #ffffffb0,
    inset 3px 3px 5px rgba(94, 104, 121, 0.692);
}

svg {
  width: 100%;
  height: 100%;
  position: relative;
}

svg circle {
  position: absolute;
  transform: scale(0.939);
  transform-origin: center;
  fill: none;
  stroke: url(#GradientColor);
  stroke-width: 30px;
  stroke-dasharray: 630px;
  stroke-dashoffset: 630px;
}

.ring:nth-child(1) svg circle {
  animation: bar_1 2s linear forwards;
}

.ring:nth-child(2) svg circle {
  animation: bar_2 2s linear forwards;
}

.ring:nth-child(3) svg circle {
  animation: bar_3 2s linear forwards;
}

.ring:nth-child(4) svg circle {
  animation: bar_4 2s linear forwards;
}

.ring:nth-child(5) svg circle {
  animation: bar_5 2s linear forwards;
}

.ring:nth-child(6) svg circle {
  animation: bar_6 2s linear forwards;
}

.ring-two:nth-child(1) svg circle {
  animation: bar_7 2s linear forwards;
}

.ring-two:nth-child(2) svg circle {
  animation: bar_8 2s linear forwards;
}

.ring-two:nth-child(3) svg circle {
  animation: bar_9 2s linear forwards;
}

.ring-two:nth-child(4) svg circle {
  animation: bar_10 2s linear forwards;
}

.ring-two:nth-child(5) svg circle {
  animation: bar_11 2s linear forwards;
}

.ring-two:nth-child(6) svg circle {
  animation: bar_12 2s linear forwards;
}

.ring h1 {
  position: absolute;
  font-size: 2rem;
  background: linear-gradient(to top, #ffe838, #fd57bf);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ring-two h1 {
  position: absolute;
  font-size: 2rem;
  background: linear-gradient(to top, #ffe838, #fd57bf);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#number {
  font-weight: 600px;
