/** Portfolio */

#portfolio-item {
  margin-bottom: 3rem;
}

/** Tabs  */

.Tabs {
  font-size: 2rem;
  font-weight: 600;
  color: purple;
  padding: 15px;
  text-align: center;
  width: 100%;
  border-radius: 3rem;
  cursor: pointer;
  box-sizing: content-box;
  position: relative;
}

/** Tab Navigation */
.tab {
  width: 80%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 2rem;
  background: linear-gradient(to right, #ffe838, #fd57bf);
}

.tab li {
  width: 50%;
  padding: 1rem;
  list-style: none;
  text-align: center;
  cursor: pointer;
  transition: all 0.7s;
  margin: 0;
}

.tab li:first-child {
  border-bottom-left-radius: 2rem;
  border-top-left-radius: 2rem;
}

.tab li:last-child {
  border-bottom-right-radius: 2rem;
  border-top-right-radius: 2rem;
}

.tab li.active {
  background: linear-gradient(to right, #e91e63, #673ab7);
  color: #eee;
}

/** Tab Content Styles */
.TabContent {
  font-size: 2rem;
  text-align: center;
  flex-grow: 1;
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(35rem, 1fr));
  grid-gap: 2.5rem;
}

/** Card */

.card img {
  width: 100%;
  border-top-left-radius: 0.7rem;
  border-top-right-radius: 0.7rem;
}

.card {
  background: #16162d;
  box-shadow: 0 0 10px #000;
  text-align: center;
  cursor: pointer;
  border-radius: 0.7rem;
}

.card h1 {
  font-size: 6rem;
  font-family: "Brush Script MT", sans-serif;
  background: linear-gradient(to top, #ffe838, #fd57bf);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.card-social-media {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.card-social-media i {
  font-size: 5rem;
  transition: color 650ms;
}

.card .fa-github:hover {
  color: #1DB954;
}

.card .fa-github {
  margin-right: 1rem;
}

.fa-podcast:hover {
  color: red;
}

.fa-podcast {
  margin-left: 1rem;
}

.title {
  color: gray;
}

.using {
  flex-grow: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(1rem, 1fr));
  grid-gap: 1rem;
  text-align: center;
  justify-content: center;
  margin-left: 1rem;
  margin-right: 1rem;
}

.using-name {
  width: auto;
  background: linear-gradient(to right, #e91e63, #673ab7);
  border-radius: 0.5rem;
  margin-bottom: 0.7rem;
  margin-top: 1rem;
  display: flex;
  text-align: center;
  justify-content: center;
}

.using-name h6 {
  color: #16162d;
  font-size: 2rem;
}

