/** Timeline */

.timeline ul {
  border-left: 4px solid #ffe838;
  border-radius: 0.8rem;
  background-color: rgba(0, 0, 0, 0.05);
  margin: 0 auto;
  position: relative;
  padding: 5rem;
  text-align: left;
  width: 61.5%;
}

.timeline h1 {
  font-size: 2rem;
  text-transform: uppercase;
  font: 300;
  margin-bottom: 1rem;
  opacity: 0.3;
}

.timeline .date {
  border-bottom: 1px solid rgba(255, 255, 055, 0.1);
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  position: relative;
}

.timeline .date:last-of-type {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none;
}

.timeline .date::before,
.timeline .date::after {
  position: absolute;
  display: block;
  top: 50%;
  transform: translateY(-50%);
}

.timeline .date::before {
  content: attr(data-date);
  left: -21rem;
  text-align: right;
  min-width: 12rem;
  font-size: 1.5rem;
}

.timeline .date::after {
  content: "";
  box-shadow: 0 0 0 4px #fd57bf;
  border-radius: 100%;
  left: -7.9rem;
  height: 1.5rem;
  width: 1.5rem;
  background-color: #313534;
}

/** Timeline */

/** Contact */

.contact {
  background-color: #221e3f;
}

form {
  width: 70%;
  margin: 0 auto;
}

label {
  font-size: 1.5rem;
}

input,
select,
textarea {
  width: 100%;
  padding: 1rem;
  margin-bottom: 5px;
  border: none;
  outline-color: #fd57bf;
  border-radius: 0.4rem;
}

input[type='submit'] {
  width: initial;
  background-color: rgb(89, 0, 170);
  color: #ffe838;
  padding: 1rem 2rem;
  border-radius: 0.8rem;
  cursor: pointer;
  transition: background-color 250ms;
  margin: auto;
  display: block;
  text-align: center;
}

input[type='submit']:hover {
  color: #eee;
  background-color: #385af3;
}

