/** Footer */

.copyright {
  width: 100%;
  text-align: center;
  background-color: #16162d;
  padding: 1.5rem 0;
  position: relative;
}

.up {
  position: absolute;
  width: 5rem;
  height: 5rem;
  background-color: #eee;
  top: -2.5rem;
  right: 5rem;
  border-radius: 100%;
  display: flex;
  animation: pulse 2s infinite;
  cursor: pointer;
}

.up i {
  color: #16162d;
  font-size: 2rem;
  margin: auto;
}

.copyright p {
  font-size: 1.45rem;
}

#up,
html,
body {
  scroll-behavior: smooth;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(253, 87, 191, 0.99);
  }

  70% {
    box-shadow: 0 0 0 2rem rgba(253, 87, 191, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(253, 87, 191, 0.99);
  }
}

